.navbar{
  position: absolute !important;
  top: 40px;
  width: 85%;
  margin: 0 9%;
  background-color: #404040;
  border-radius: 2.5em;
  padding: 20px;
  z-index: 22;
}

.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover, .navbar-light .navbar-nav .current {
  color: blue !important;
}


.navbar-light .navbar-nav .nav-link {
  color: #ffffff !important;
}

.resume-btn{
  text-decoration: none;
  color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 2.5em;
  padding: 15px;
  font-size: 11pt;
  margin-left: 540px;
}

.center-side{
  background-color: #0D0D0D;
  border-right: 1px solid gray;
}

.cover-img{
  margin-top: 100px;
}

.right-side{
  /* position: fixed;
  right: 0;
  bottom: 0; */
  background-color: #0D0D0D;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.right-side .linkedin,.right-side .github , .right-side .email {
  text-decoration: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0D0D0D;
  border: 1px solid #404040;
  margin-bottom: 15px;
  font-size: 14pt;
  color: #ffffff;
}

.linkedin:hover ,.github:hover ,.email:hover{
  background-color: #404040;
  cursor: pointer;
}

@media screen and (max-width: 1024px){
  .resume-btn{
    margin: 0;
   display: block;
 }
}

@media screen and (max-width: 768px) {
  .left-side{
    display: none;
  }
  .right-side{
    flex-direction: row;
    justify-content: space-evenly;
  }
  .resume-btn{
     margin: 0;
    display: block;
  }
}
